import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import DatePicker from 'react-datepicker';
import { useState } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';

function AddOrUpdate() {
  const [startDate, setStartDate] = useState(new Date());
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content batteries__add__update">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">고장신고 접수 {param.flag}</dt>
          </dl>
        </div>
        <div className="right btn__wrap">
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">삭제</span>
          </button>
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">저장</span>
          </button>
        </div>
      </div>
      {/* // sub_nav */}
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul>
            <li>
              <dl>
                <dt>프로젝트 지정</dt>
                <dd>
                  <select className="custom__select">
                    <option>BSS</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>장치명</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>발생일시</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>고객명</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>고객ID</dt>
                <dd>
                  <input type="email" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>휴대전화번호</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>장애타입</dt>
                <dd>
                  <select className="custom__select">
                    <option>LG에너지솔루션</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>제목</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>내용</dt>
                <dd>
                  <textarea className="custom__input" />
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>첨부파일</dt>
                <dd>
                  <textarea className="custom__input" />
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>답변</dt>
                <dd>
                  <textarea className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>현직담당자 배정</dt>
                <dd>
                  <select className="custom__select">
                    <option>정상</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>조치예정일</dt>
                <dd>
                  <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
